  const Chemistryvideo =`https://edu.uzagrolab.uz${pest['all_protect'] ? pest.all_protect.chemistry_vedio: null}`

   const [pestIdInformation, setPestIdInformation] = useState([]);

  useEffect(() => {
    axios.get(`https://edu.uzagrolab.uz/api/alldataa/${pestId}/`)
    .then((res) => {
      setPestIdInformation(res.data)
      setPest(res.data)
    })
  },[])
  //  const getPestResearch = async pestId => {
  //   setIsLoading(true);
  //   axios.get(`http://127.0.0.1:8000/api/alldata/${pestId}/`)
  //   .then((res) => {
  //     setPestIdInformation(res.data)
  //   });
  //  }
 





  // const getPestProduct = async pestId => {
  //   const pestProduct = await fetchRequest.get(`product/${pestId}/`)
  //   .then(res => res.data);
  //   return pestProduct;
  // };

  // const getPestPheno = async pestId => {
  //   const pestPheno = await fetchRequest.get(`phenology/${pestId}`).then(res => res.data);
  //   return pestPheno;
  // };

  // const getPestProtect = async pestId => {
